.item {
	color: #3a1b14;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: baseline;
	margin-bottom: 10px;
}

.item::before {
	content: "\2022";
}

.button {
	margin-top: 10px;
	/* width: 200px; */
	border-radius: 4px;
	background-color: white;
	color: #143a1d;
}

.button:hover,
.button:focus {
	background-color: #143a1d;
	color: white;
}
